body {
    margin: 0;
    padding: 0;
    font-family: -apple-system;
    background-color: lightblue;
}

.App {
    background-color: white;
    max-width: 680px;
    margin: auto;
}

.Navbar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: hotpink;
    box-shadow: 3px 3px 3px #333;
}

@media only screen and (max-width : 600px){
    .Navbar {
        display: grid;
        grid-template-columns: 1fr;
    }

    .nav-menu {
        display: grid;
        grid-template-columns: 1fr;
        padding:0;
    }

    .nav-menu li a{
        display: block;
        text-align: center;
        margin: 2px;
    }
}

.nav-menu{
    list-style-type: none;
    margin-top: auto;
    margin-bottom: auto;
}

.logo-link a {
    color: white;
    text-decoration: none;
    margin: 10px;
}

.logo-link a:hover {
    color: black;
    transition: all 1s ease-in-out;
}

.nav-menu li {
    display: inline-block;
}

.nav-menu li a {
    text-decoration: none;
    background-color: white;
    padding: 10px;
    border-radius: 2px;
    margin-right: 5px;
}

.nav-menu li a:hover {
    background-color: black;
    color: white;
    transition: 1s ease-in-out;
}
h2 {
    text-align: center;
}

.home-image {
    width: 100%;
    height: 300px;
    background-image: url(/SinglePageApp/static/media/oscars1.f6cb9274.jpg);
    background-size: cover;
    background-position: center;
    margin: auto;
    transition: all 1s ease-in-out;
}

.home-image:hover {
    width: 90%;
    background-position: center;
    margin: auto;
    transition: all 1s ease-in-out;
}

.thumbnail-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 30px 0px 30px 0px;
}

.thumbnail-container div {
    width: 170px;
    height: 120px; 
    transition: 1s;
}

.thumbnail-container div:hover {
    opacity: 0.8;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: 1s;
}

.thumbnail-1, .thumbnail-2, .thumbnail-3 {
    background-size: cover;
    width: 100%;
}

.thumbnail-1 {
    background-image: url(/SinglePageApp/static/media/oscarWinners1.4a4a17d6.jpg);
}

.thumbnail-2 {
    background-image: url(/SinglePageApp/static/media/oscarWinners2.4cae0712.jpg);
}

.thumbnail-3 {
    background-image: url(/SinglePageApp/static/media/oscarWinners3.cffa8126.jpg);
}


